body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
    background-color: red;
}



/* movie detail */

.movie-detail-container {
    background: #113049;
    width: 100%;
    height: 100%;
}

.movie-detail {
    background: #232d32;
    opacity: 0.9;
    padding: 15% 20% 20% 20%;
}

.movie-detail h1 {
    color: rgb(241, 239, 247);
    text-align: left;
    font-size: 3.5em;
}

.rating {
    display: inline;
    text-align: left;
    color: #ECEBEE;
    text-align: left;
}

span, .genres, .release-date {
    color: #9EB3C4;
}

.genres, .genre, .release-date {
    padding: 5px;
}

.genres {
    display: flex;
}

.overview {
    color: rgb(210, 209, 211);
    text-align: left;
    padding-top: 3%;
    padding-bottom: 7%;
}

.images {
    display: flex;
}

.image {
    width: 154px;
    height: 200px;
    margin: 2%;
    text-align: left;
}

.inlining {
    display: flex;
    align-items: center;
}
/* page container */
.page-container {
    max-width: 1170px;
    margin: 0 auto;
    padding-left: 16px;
    padding-right: 16px;
}

/* movie container */
.grid-container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    grid-column-gap: 20px;
    grid-row-gap: 40px;
    padding-bottom: 20px;
} 
h1{
    margin-top: 0;
    padding-top: 56px;
    color: #FDFEFF;
}
hr {
    margin-top: 40px;
    margin-bottom: 50px;
    color: #303E4B;
}

/* movie card */

.image-card {
    position: relative;
}
.movie-card-top {
    border-radius: 5px;
    max-width: 100%;
}
.rating {
    width: 45px;
    height: 45px;
    position: absolute;
    top: 20px;
    right: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    background-color: #74D5AA;
    color: #294448; 
    pointer-events: none; 
}
.movie-release-date {
    color: #666B73;
    padding-top: 20px;
}
.movie-title {
    color: #FAFAFC; 
    font-size: 20px;
}

/* Design for tablets and phones */
/* Always add media queries at the bottom of the css file to avoid overriding issues */
@media (max-width: 900px) {
    .grid-container {
        grid-template-columns: 1fr 1fr 1fr;
    }
} 
@media (max-width: 600px) {
    .grid-container {
        grid-template-columns: 1fr 1fr;
    }
}
@media (max-width: 375px) {
    .grid-container {
        grid-template-columns: 1fr;
    }
}

